.paginationDiv {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    ul{
        display: flex;
        list-style: none;
        width: 100%;
    }

    .pageTextStyling {
      color: #333;
      text-align: center;
      font-size: 14px;
      font-family: "Roboto";
      line-height: 20px;
    }

    a {
      color: #333 !important;
      font-size: 14px !important;
    }

    .active {
      background-color: #0174ff;
      a {
        color: white !important;
        font-size: 14px !important;
      }
    }

    .prev {
      padding: 3px;
      border-top-left-radius: 4px;
      border-bottom-left-radius: 4px;
      background-color: white;
      border: 0.5px solid rgba(170, 170, 170, 0.515);
      width: 5%;
      text-align: center;
      cursor: pointer;
    }

    .next {
      padding: 3px;
      border-top-right-radius: 4px;
      border-bottom-right-radius: 4px;
      background-color: white;
      border: 0.5px solid rgba(170, 170, 170, 0.515);
      width: 5%;
      text-align: center;
      cursor: pointer;
    }

    .page-item {
      padding: 3px;
      border: 0.5px solid rgba(170, 170, 170, 0.515);
      cursor: pointer;
      width: 5%;
      text-align: center
      
      
    }
  }